import { colors } from "../styleguide";

interface PrimaryButtonColorOptions {
	default: string
	hoverAndActive: string
}

export type PrimaryButtonColors = "blue" | "green" | "black" | "destructive";

export const primaryButtonColors: Record<PrimaryButtonColors, PrimaryButtonColorOptions> = {
	blue: {
		default: colors.midBlue,
		hoverAndActive: "#3144A3",
	},
	green: {
		default: colors.teal,
		hoverAndActive: "#21A68D",
	},
	black: {
		default: colors.black,
		hoverAndActive: colors.blackGray,
	},
	destructive: {
		default: colors.red,
		hoverAndActive: "#E63A2E",
	},
};

export const getPrimaryButtonContentColor = (color: PrimaryButtonColors): string => {
	if (color === "green" || color === "destructive") return colors.black;
	return colors.white;
};
